.book-slider-container {
    position: relative;
    width: 100%;
    margin: 20px 0;
    padding: 0 30px;

    .slider-wrapper {
        display: flex;
        overflow-x: auto;
        scroll-behavior: smooth;
        gap: 12px;
        padding: 8px 2px;
        -ms-overflow-style: none;
        scrollbar-width: none;

        &::-webkit-scrollbar {
            display: none;
        }
    }

    .book-item {
        flex: 0 0 calc(20% - 12px); /* 5Í∞ú Ìï≠Î™©Îßå Î≥¥Ïù¥ÎèÑÎ°ù */
        background: #fff;
        border-radius: 4px;
        border: 1px solid #e8e8e8;
        box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
        display: flex;
        flex-direction: column;
        overflow: hidden;
        transition: transform 0.2s ease;

        &:hover {
            transform: translateY(-2px);
            box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
        }

        .book-cover-container {
            width: 100%;
            aspect-ratio: 3/4;
            overflow: hidden;
            background: #f5f5f5;
            position: relative;

            .book-cover {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

            .cover-placeholder {
                width: 100%;
                height: 100%;
                background: #ebebeb;
                display: flex;
                align-items: center;
                justify-content: center;
                color: #999;
                font-size: 12px;

                &.empty {
                    background: #f0f0f0;
                }
            }
        }

        .book-info {
            padding: 6px 8px;
            display: flex;
            flex-direction: column;
            gap: 4px;

            .tag-section {
                min-height: 18px;
                display: flex;
                flex-wrap: wrap;
                gap: 4px;

                .book-tag {
                    background: #f0f0f0;
                    color: #666;
                    padding: 2px 6px;
                    border-radius: 3px;
                    font-size: 11px;
                    line-height: 1.2;
                    max-width: 60px;
                }
            }

            .book-title {
                font-size: 14px;
                font-weight: 600;
                line-height: 1.3;
                margin: 0;
                min-height: 36px;
                color: #333;
            }

            .book-meta {
                font-size: 12px;
                color: #666;
                min-height: 14px;
            }

            .price-section {
                display: flex;
                gap: 4px;
                .discount-rate {
                    font-size: 13px;
                }
                .final-price {
                    font-size: 13px;
                }
            }

            .review-section {
                font-size: 12px;
            }
        }
    }

    .slider-button {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 40px; /* Î≤ÑÌäº ÌÅ¨Í∏∞ ÌÇ§ÏõÄ */
        height: 40px; /* Î≤ÑÌäº ÌÅ¨Í∏∞ ÌÇ§ÏõÄ */
        background: #fff;
        border: 2px solid #333; /* ÌÖåÎëêÎ¶¨ ÏÉâÏÉÅ Î≥ÄÍ≤Ω */
        border-radius: 50%;
        box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1); /* Í∑∏Î¶ºÏûê Í∞ïÏ°∞ */
        cursor: pointer;
        font-size: 18px;
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 20; /* Îçî ÏúÑÎ°ú Ïò¨Î¶¨Í∏∞ */
        transition: all 0.2s ease;

        &:hover {
            background: #f5f5f5;
            transform: translateY(-50%) scale(1.1); /* Î≤ÑÌäº ÌÅ¨Í∏∞ ÌôïÎåÄ */
        }

        &.left {
            left: 10px;
        }

        &.right {
            right: 10px;
        }
    }
}

@media (max-width: 1200px) {
    .book-slider-container {
        padding: 0 20px;

        .book-item {
            flex: 0 0 calc(20% - 12px); /* ÌôîÎ©¥Ïóê 5Í∞úÎßå Î≥¥Ïù¥ÎèÑÎ°ù ÏÑ§Ï†ï */
        }

        .slider-button {
            width: 40px;
            height: 40px;
        }
    }
}

@media (max-width: 768px) {
    .book-slider-container {
        padding: 0 20px;

        .book-item {
            flex: 0 0 calc(33.33% - 12px); /* ÏûëÏùÄ ÌôîÎ©¥ÏóêÏÑúÎäî 3Í∞ú Ìï≠Î™©ÏúºÎ°ú ÏÑ§Ï†ï */
        }

        .slider-button {
            width: 32px;
            height: 32px;
        }
    }
}
